<template>

  <tr>
    <td>{{employee.name}}
      <i class="fa fa-pencil" aria-hidden="true" v-if="!correction" @click="correction = true"></i>
      <i class="fa fa-check" aria-hidden="true" v-else="correction" @click="changeItem"></i>
      <i class="fa fa-times" aria-hidden="true" v-show="correction" @click="$emit('remove-item')"></i>
    </td>
    <td>{{employee.position}}</td>
    <td class="table-date" :class="classDate" v-if="!correction">{{employee.firstDay}}</td>
    <td class="table-input-td table-date" v-else="correction">
      <input type="date" class="table-input" v-model="employee.firstDay">
    </td>
    <td class="table-date" :class="classDate" v-if="!correction">{{employee.lastDay}}</td>
    <td class="table-input-td table-date" v-else="correction">
      <input type="date" class="table-input" v-model="employee.lastDay">
    </td>
  </tr>

</template>


<script>
  /* eslint-disable */
  import Employees from '../../static/data.json'

  export default {
    name: 'Employee',
    props: ['employee'],
    data() {
      return {
        allEmployees: Employees.employees,
        correction: false,
        employees: [],
        classDate: {
          'table-danger': false,
          'table-warning': false,
          'table-success': false
        }
      }
    },
    computed: {
      minDay() {
        const DAY = 86400000;
        return (Date.parse(this.employee.lastDay) - Date.parse(this.employee.firstDay) >= DAY)
      },
      requiredFields() {
        return !!(this.employee.firstDay && this.employee.lastDay)
      },
      maxDay() {
        const DAYS_15 = 1296000000;
        const DAY = 86400000;
        return (Date.parse(this.employee.lastDay) - Date.parse(this.employee.firstDay) + DAY <= DAYS_15)
      },
      maxInYear() {
        const DAY = 86400000;
        const MAX_DAY_IN_YEAR = 24;
        const allHolidays = [];
        let daysInYear = 0;
        let yearAgo;
        let rest;

        this.getEmployees();
        for (let i = 0; i < this.employees.length; i += 1) {
          if (this.employee.name === this.employees[i].name) {
            allHolidays.push(this.employees[i])
          }
        }
        if (allHolidays.length === 0) return true;
        yearAgo = this.employee.firstDay.split('-');
        yearAgo[0] -= 1;
        yearAgo = yearAgo.join('-');

        for (let i = 0; i < allHolidays.length; i += 1) {
          if (Date.parse(yearAgo) <= Date.parse(allHolidays[i].firstDay)) {
            rest = (Date.parse(allHolidays[i].lastDay) - Date.parse(allHolidays[i].firstDay) + DAY) / DAY;
            daysInYear += rest;
          }
        }
        let daysInNewHolidays = (Date.parse(this.employee.lastDay) - Date.parse(this.employee.firstDay) + DAY) / DAY;

        return !((daysInYear + daysInNewHolidays) > MAX_DAY_IN_YEAR)

      },
      minInterval() {
        const allHolidays = [];
        const startHoliday = Date.parse(this.employee.firstDay);
        const endHoliday = Date.parse(this.employee.lastDay);

        this.getEmployees();

        for (let i = 0; i < this.employees.length; i += 1) {
          if (this.employee.id === this.employees[i].id) continue;
          if (this.employee.name === this.employees[i].name) {
            allHolidays.push(this.employees[i])
          }
        }
        if (allHolidays.length === 0) return true;

        for (let i = 0; i < allHolidays.length; i += 1) {

          const startClosestHoliday = Date.parse(allHolidays[i].firstDay);
          const endClosestHoliday = Date.parse(allHolidays[i].lastDay);

          if (Math.abs(startHoliday - endClosestHoliday) < Math.abs(startClosestHoliday - endHoliday)) {
            if ((startHoliday - endClosestHoliday) > endClosestHoliday - startClosestHoliday)  {
              return true;
            }
          } else {
            if ((endHoliday - startHoliday) < startClosestHoliday - endHoliday) return true;
          }
        }
        return false;

      },
      halfEmployees() {
        const allHolidaysPosition = [];
        const startHoliday = Date.parse(this.employee.firstDay);
        const endHoliday = Date.parse(this.employee.lastDay);
        let positionLength = 0;
        let halfEmployees;
        let currentHoliday = 0;
        this.getEmployees();

        for (let i = 0; i < this.allEmployees.length; i += 1) {
          if (this.allEmployees[i].position === this.employee.position) {
            positionLength += 1;
          }
        }
        halfEmployees = Math.floor(positionLength / 2);

        for (let i = 0; i < this.employees.length; i += 1) {
          if (this.employee.position === this.employees[i].position) {
            allHolidaysPosition.push(this.employees[i])
          }
        }

        for (let i = 0; i < allHolidaysPosition.length; i += 1) {
          if (allHolidaysPosition[i].id === this.employee.id) continue;
          const startCurrent = Date.parse(allHolidaysPosition[i].firstDay);
          const endCurrent = Date.parse(allHolidaysPosition[i].lastDay);

          if ((startHoliday >= startCurrent && startHoliday <= endCurrent)
            || (endHoliday >= startCurrent && endHoliday <= endCurrent)
            || (startCurrent >= startHoliday && startCurrent <= endHoliday)
            || (endCurrent >= startHoliday && endCurrent <= endHoliday)
          ) {
            currentHoliday += 1;
          }
        }
        return (halfEmployees > currentHoliday);
      }
    },
    methods: {
      changeItem() {
        switch (false) {
          case this.requiredFields:
            this.$root.$emit('eEmptyFields');
            break;
          case this.maxDay:
            this.$root.$emit('eMaxDay');
            break;
          case this.minDay:
            this.$root.$emit('eMinDay');
            break;
          case this.minInterval:
            this.$root.$emit('eMinInterval');
            break;
          case this.maxInYear:
            this.$root.$emit('eMaxInYear');
            break;
          case this.halfEmployees:
            this.$root.$emit('eHalfEmployees');
            break;
        }

        if (this.requiredFields
          && this.minDay
          && this.maxDay
          && this.maxInYear
          && this.minInterval
          && this.halfEmployees) {

          this.correction = false;
          this.$emit('change-item')
        }
      },
      changeStyle() {

        const date = Date.parse(new Date().getFullYear() +
          '-' + (new Date().getMonth() + 1) + '-' + new Date().getDate());

        Date.parse(this.employee.firstDay) > date
          ? this.classDate['table-warning'] = true
          : this.classDate['table-warning'] = false;

        Date.parse(this.employee.lastDay) < date
          ? this.classDate['table-danger'] = true
          : this.classDate['table-danger'] = false;

        Date.parse(this.employee.firstDay) <= date && Date.parse(this.employee.lastDay) >= date
          ? this.classDate['table-success'] = true
          : this.classDate['table-success'] = false;
      },
      getEmployees() {
        this.employees = JSON.parse(localStorage.getItem('holidays')) || []
      },
    },
    mounted() {
      const intervalUpdate = 5000;
      this.changeStyle();
      setInterval(this.changeStyle, intervalUpdate);
    }
  }
</script>

<style lang="scss">
  tr {
    transition: all ease .3s;
    &:hover {
      .fa {
        opacity: 1;
      }
    }
  }

  td {
    position: relative;
  }

  .fa {
    opacity: 0;
    top: 15px;
    right: 13px;
    position: absolute;
    color: #000;
    transition: all ease .3s;
    cursor: pointer;
    &:hover {
      color: #2194e7;
    }
  }

  .fa-fa-pencil {
    top: 15px;
    right: 13px;
  }

  .fa-times {
    font-size: 1.2em;
    top: 14px;
    right: 37px;

    &:hover {
      color: red;
    }
  }

  .table .table-input-td {
    padding: 0;
  }

  input.table-input {
    text-align: left;
    padding: 9px;
    line-height: inherit;
    width: 100%;
    box-sizing: border-box;
  }

  .table-date {
    width: 200px;
  }

</style>



