<template>

  <form class="change-holiday">
  <div class="col-12"> <h4 class="title">Добавить отпуск</h4></div>
    <div class="form-group row">
      <label class="col-sm-2" for="name">Выбрать сотрудника:</label>
      <div class="col-sm-4">
        <select class="form-control" v-model="newItem" id="name">
          <option v-for="item in allEmployees" :value="item">{{item.name}}</option>
        </select>
      </div>

      <div class="col-sm-2" v-show="newItem.name">
        <p>Должность:</p>
      </div>
      <div class="col-sm-4">{{newItem.position}}</div>
    </div>

    <div class="form-group row" v-show="newItem.name">

      <label for="firstDay" class="col-sm-2 col-form-label">Начало отпуска:</label>
      <div class="col-sm-4">
        <input type="date" v-model="newItem.firstDay" class="form-control" id="firstDay" placeholder="yyyy-mm-dd">
      </div>

      <label for="lastDay" class="col-sm-2 col-form-label">Конец отпуска:</label>
      <div class="col-sm-4">
        <input type="date" v-model="newItem.lastDay" class="form-control" id="lastDay" placeholder="yyyy-mm-dd">
      </div>

    </div>

    <div class="form-row" v-show="newItem.name">
      <div class="col">
        <button type="button" class="btn btn-primary btn-block add-item" @click.prevent="addItem">Добавить</button>
      </div>
    </div>


  </form>

</template>


<script>
  /* eslint-disable */
  import Employees from '../../static/data.json'

  export default {
    name: 'AddHoliday',
    data() {
      return {
        allEmployees: Employees.employees,
        employees: [],
        selected: '',
        currentId: 0,
        newItem: {
          id: '',
          name: '',
          position: '',
          firstDay: '',
          lastDay: ''
        }
      }
    },
    computed: {
      minDay() {
        const DAY = 86400000;
        return (Date.parse(this.newItem.lastDay) - Date.parse(this.newItem.firstDay) >= DAY)
      },
      maxDay() {
        const DAYS_15 = 1296000000;
        const DAY = 86400000;
        return (Date.parse(this.newItem.lastDay) - Date.parse(this.newItem.firstDay) + DAY <= DAYS_15)
      },
      requiredFields() {
        return !!(this.newItem.firstDay && this.newItem.lastDay)
      },
      maxInYear() {
        const DAY = 86400000;
        const MAX_DAY_IN_YEAR = 24;
        const allHolidays = [];
        let yearAgo;
        let daysInYear = 0;
        let rest;
        this.getEmployees();

        for (let i = 0; i < this.employees.length; i += 1) {
          if (this.newItem.name === this.employees[i].name) {
            allHolidays.push(this.employees[i])
          }
        }
        if (allHolidays.length === 0) return true;
        yearAgo = this.newItem.firstDay.split('-');
        yearAgo[0] -= 1;
        yearAgo = yearAgo.join('-');


        for (let i = 0; i < allHolidays.length; i += 1) {
          if (Date.parse(yearAgo) <= Date.parse(allHolidays[i].firstDay)) {
            rest = (Date.parse(allHolidays[i].lastDay) - Date.parse(allHolidays[i].firstDay) + DAY) / DAY;
            daysInYear += rest;
          }
        }
        let daysInNewHolidays = (Date.parse(this.newItem.lastDay) - Date.parse(this.newItem.firstDay) + DAY) / DAY;

        return !((daysInYear + daysInNewHolidays) > MAX_DAY_IN_YEAR)

      },
      minInterval() {
        const allHolidays = [];
        const startHoliday = Date.parse(this.newItem.firstDay);
        const endHoliday = Date.parse(this.newItem.lastDay);
        this.getEmployees();

        for (let i = 0; i < this.employees.length; i += 1) {
          if (this.newItem.name === this.employees[i].name) {
            allHolidays.push(this.employees[i])
          }
        }
        if (allHolidays.length === 0) return true;

        for (let i = 0; i < allHolidays.length; i += 1) {

          const startClosestHoliday = Date.parse(allHolidays[i].firstDay);
          const endClosestHoliday = Date.parse(allHolidays[i].lastDay);

          if (Math.abs(startHoliday - endClosestHoliday) < Math.abs(startClosestHoliday - endHoliday)) {
              if ((startHoliday - endClosestHoliday) > endClosestHoliday - startClosestHoliday)  {
                return true;
              }
          } else {
              if ((endHoliday - startHoliday) < startClosestHoliday - endHoliday) return true;
          }
        }
        return false;

      },
      halfEmployees() {
        const allHolidaysPosition = [];
        let positionLength = 0;
        let halfEmployees;
        let currentHoliday = 0;
        this.getEmployees();

        for (let i = 0; i < this.allEmployees.length; i += 1) {
          if (this.allEmployees[i].position === this.newItem.position) {
            positionLength += 1;
          }
        }
        halfEmployees = Math.floor(positionLength / 2);

        for (let i = 0; i < this.employees.length; i += 1) {
          if (this.newItem.position === this.employees[i].position) {
            allHolidaysPosition.push(this.employees[i])
          }
        }

        const startHoliday = Date.parse(this.newItem.firstDay);
        const endHoliday = Date.parse(this.newItem.lastDay);

        for (let i = 0; i < allHolidaysPosition.length; i += 1) {
          const startCurrent = Date.parse(allHolidaysPosition[i].firstDay);
          const endCurrent = Date.parse(allHolidaysPosition[i].lastDay);

          if ((startHoliday >= startCurrent && startHoliday <= endCurrent)
            || (endHoliday >= startCurrent && endHoliday <= endCurrent)
            || (startCurrent >= startHoliday && startCurrent <= endHoliday)
            || (endCurrent >= startHoliday && endCurrent <= endHoliday)
          ) {
            currentHoliday += 1;
          }
        }
        return (halfEmployees > currentHoliday);
      }
    },
    methods: {
      addItem() {
        switch (false) {
          case this.requiredFields:
            this.$root.$emit('eEmptyFields');
            break;
          case this.maxDay:
            this.$root.$emit('eMaxDay');
            break;
          case this.minDay:
            this.$root.$emit('eMinDay');
            break;
          case this.minInterval:
            this.$root.$emit('eMinInterval');
            break;
          case this.maxInYear:
            this.$root.$emit('eMaxInYear');
            break;
          case this.halfEmployees:
            this.$root.$emit('eHalfEmployees');
            break;
        }
        if (this.requiredFields
          && this.maxDay
          && this.minDay
          && this.minInterval
          && this.maxInYear
          && this.halfEmployees) {

          this.newItem.id = (this.currentId += 1);
          this.employees.push(this.newItem);
          this.setEmployees(this.employees);
          this.newItem = {
            id: '',
            name: '',
            position: '',
            firstDay: '',
            lastDay: ''
          };
          this.$root.$emit('add-item')
        }
      },
      getCurrentId() {
        this.getEmployees();
        if (this.employees.length === 0) this.currentId = 0;
        else {
          let max = 1;
          for (let i = 0; i < this.employees.length; i += 1) {
            if (this.employees[i].id > max) max = this.employees[i].id
          }
          this.currentId = max;
        }
      },
      getEmployees() {
        this.employees = JSON.parse(localStorage.getItem('holidays')) || []
      },
      setEmployees(value) {
        localStorage.setItem('holidays', JSON.stringify(value))
      }
    },
    mounted() {
      this.getCurrentId();
    }
  }
</script>


<style lang="scss">

  .change-holiday {
    margin: 30px 0;
    padding: 50px 40px;
    background-color: #fff;
  }

  .add-item {
    margin-top: 20px;
  }

  .position {
    text-align: right;
  }
  .title {
    text-align: center;
    padding-bottom: 40px;
  }
  .form-group.animated {
    animation-duration: .8s;
    animation-fill-mode: both;
  }

</style>



