<template>
  <div class="table-container">
    <table class="table table-hover table-bordered table-responsive">
      <thead>
      <tr>
        <th scope="col" class="head-name" @click="sortByName">ФИ</th>
        <th scope="col">Должность</th>
        <th scope="col" class="head-start-holidays" @click="sortByDate">Начало отпуска</th>
        <th scope="col">Конец отпуска</th>
      </tr>
      </thead>
      <tbody>
      <employee v-for="(item, index) in employees"
                :key="item.id"
                :employee="item"
                @remove-item="removeItem(index)"
                @change-item="changeItem"
      />
      </tbody>
    </table>

  </div>
</template>


<script>
  /* eslint-disable */
  import Employee from './employee';

  export default {
    name: 'Table',
    components: {
      Employee
    },
    data() {
      return {
        nameInLS: 'holidays',
        employees: []
      }
    },
    methods: {

      getEmployees() {
        this.employees = JSON.parse(localStorage.getItem(this.nameInLS))
      },
      setEmployees(value) {
        localStorage.setItem(this.nameInLS, JSON.stringify(value))
      },
      removeItem (index) {
          this.employees.splice(index, 1);
          this.setEmployees(this.employees);
      },
      changeItem () {
        this.setEmployees(this.employees);
      },
      sortByName() {
        this.employees.sort((a, b) => {
          if (a.name < b.name) return -1;
          if (a.name > b.name) return 1;
          return 0;
        });
      },
      sortByDate () {
        this.employees.sort((a, b) => {
          if (a.firstDay < b.firstDay) return -1;
          if (a.firstDay > b.firstDay) return 1;
          return 0;
        });
      }
    },
    mounted() {
      this.getEmployees();
      this.$root.$on('add-item', () => {
        this.getEmployees()
      })
    }
  }
</script>


<style lang="scss">
  .table-container {
    margin-top: 30px;
    padding: 30px 30px;
    background-color: #fff;
  }

  .table-bordered {
    margin: 0;
  }

  .head-name {
    cursor: pointer;
  }

  .head-start-holidays {
    cursor: pointer;
  }

</style>



