<template>
  <transition name="custom-transition"
              enter-active-class="animated zoomIn"
              leave-active-class="animated zoomOut">

  <div class="error-notification" v-if="showing">
    <p class="error-title">Ошибка</p>
    <p class="message">{{message}}</p>
  </div>
  </transition>
</template>

<script>
  /* eslint-disable */
  export default {
    name: 'ErrorNotification',
    data() {
      return {
        showing: false,
        message: '',
        errorMessages: {
          eEmptyFields: 'Выберите дату',
          eMaxDay: 'Максимальное количество дней в одном отпуске - 15',
          eMinDay: 'Минимальное количество дней в одном отпуске - 2',
          eMinInterval: 'Минимальный период между отпусками должен равняться размеру предыдущего отпуска',
          eMaxInYear: 'Максимальное количество дней отпуска в одном году - 24',
          eHalfEmployees: 'в отпуске не может находится больше половины сотрудников выбранной должности'
        }
      }
    },
    methods: {
      hideNotification() {
        const TIME_SHOWING = 3000;
        setTimeout(() => {
            this.showing = false
          }, TIME_SHOWING);
      },
    },
    mounted() {
      this.$root.$on('eEmptyFields', () => {
        this.showing = true;
        this.hideNotification();
        this.message = this.errorMessages.eEmptyFields;
      } );
      this.$root.$on('eMaxDay', () => {
        this.showing = true;
        this.hideNotification();
        this.message = this.errorMessages.eMaxDay;
      });
      this.$root.$on('eMinDay', () => {
        this.showing = true;
        this.hideNotification();
        this.message = this.errorMessages.eMinDay;
      });
      this.$root.$on('eMinInterval', () => {
        this.showing = true;
        this.hideNotification();
        this.message = this.errorMessages.eMinInterval;
      });
      this.$root.$on('eMaxInYear', () => {
        this.showing = true;
        this.hideNotification();
        this.message = this.errorMessages.eMaxInYear;
      });
      this.$root.$on('eHalfEmployees', () => {
        this.showing = true;
        this.hideNotification();
        this.message = this.errorMessages.eHalfEmployees;
      })
    }
  }

</script>


<style lang="scss">
  .error-notification.animated {
    animation-duration: .3s;
    animation-fill-mode: both;
  }
  .error-notification {
    position: fixed;
    bottom: 5px;
    left: 5px;
    height: 150px;
    width: 400px;
    background-color: rgba(130, 130, 130, 0.84);
    border-radius: 10px;
    color: #ffffff;
    padding: 10px 20px;

    .error-title {
      text-align: center;
      font-weight: 600;
      font-size: 1.3em;
      padding-bottom: 10px;
    }
    .message {
      text-align: center;
    }
  }

</style>



